body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  height: 100%;
  background-color: rgb(255, 245, 233);
  
}

.header{
  text-align: center;
  color: white;
  font-weight: bold;
}

#quote-box{
  position: relative;
  height: 300px;
  max-width: 500px;
  background-color: rgb(202, 184, 133);
  margin: auto;
  text-align: center;
  border: groove ;
  color: white;
}

#text{
  
  text-align: center;

}

button{
  color: black;
}



img{
  position: absolute;
  top: 0px;
  left: 0px;
  object-fit:cover;
  height: 100vh;
  width: 100vw;
  z-index: -1;
}
